") }}]</label\r\n              >\r\n              <div class=\"col-sm-8\">\r\n                <v-select\r\n                  :class=\"{ 'ko-hop-le-select2': errors.has('monid') }\"\r\n                  v-validate=\"'required'\"\r\n                  @input=\"onMonChange()\"\r\n                  name=\"monid\"\r\n                  v-model=\"sel.monid\"\r\n                  :reduce=\"(option) => option.id\"\r\n                  :options=\"\r\n                    mons.map((x) => ({\r\n                      id: x.id,\r\n                      label: `${x.id} - ${x.tenmon}`,\r\n                    }))\r\n                  \"\r\n                />\r\n              </div>\r\n            </div>\r\n\r\n            <div class=\"form-group row\">\r\n              <label class=\"col-sm-4 col-form-label\"\r\n                >Đơn giá[{{ !errors.has(\"dongia\") }}]</label\r\n              >\r\n              <div class=\"col-sm-8\">\r\n                <input\r\n                  name=\"dongia\"\r\n                  readonly\r\n                  v-model=\"sel.dongia\"\r\n                  :class=\"{\r\n                    'form-control': true,\r\n                    'ko-hop-le': errors.has('dongia'),\r\n                  }\"\r\n                  type=\"text\"\r\n                  v-validate=\"'required'\"\r\n                />\r\n              </div>\r\n            </div>\r\n\r\n            <div class=\"form-group row\">\r\n              <label class=\"col-sm-4 col-form-label\"\r\n                >Số lượng[{{ !errors.has(\"solg\") }}]</label\r\n              >\r\n              <div class=\"col-sm-8\">\r\n                <input\r\n                  name=\"solg\"\r\n                  v-model=\"sel.solg\"\r\n                  @change=\"solgChange()\"\r\n                  :class=\"{\r\n                    'form-control': true,\r\n                    'ko-hop-le': errors.has('solg'),\r\n                  }\"\r\n                  type=\"text\"\r\n                  v-validate=\"'required|numeric'\"\r\n                />\r\n              </div>\r\n            </div>\r\n\r\n            <div class=\"form-group row\">\r\n              <label class=\"col-sm-4 col-form-label\"\r\n                >Tiền[{{ !errors.has(\"tien\") }}]</label\r\n              >\r\n              <div class=\"col-sm-8\">\r\n                <input\r\n                  name=\"tien\"\r\n                  readonly\r\n                  v-model=\"sel.tien\"\r\n                  :class=\"{\r\n                    'form-control': true,\r\n                    'ko-hop-le': errors.has('tien'),\r\n                  }\"\r\n                  type=\"text\"\r\n                  v-validate=\"'required|numeric'\"\r\n                />\r\n              </div>\r\n            </div>\r\n\r\n            <div class=\"form-group row\">\r\n              <label class=\"col-sm-4 col-form-label\"\r\n                >Bàn[{{ !errors.has(\"banid\") }}]</label\r\n              >\r\n              <div class=\"col-sm-8\">\r\n                <v-select\r\n                  :class=\"{ 'ko-hop-le-select2': errors.has('banid') }\"\r\n                  v-validate=\"'required'\"\r\n                  @input=\"onMonChange()\"\r\n                  name=\"banid\"\r\n                  v-model=\"sel.banid\"\r\n                  :reduce=\"(option) => option.id\"\r\n                  :options=\"\r\n                    bans.map((x) => ({\r\n                      id: x.id,\r\n                      label: `${x.id} - ${x.tenban}`,\r\n                    }))\r\n                  \"\r\n                />\r\n              </div>\r\n            </div>\r\n\r\n\r\n\r\n            <button type=\"submit\" class=\"btn mr-1\">Save</button>\r\n            <button type=\"button\" class=\"btn\" @click=\"hideModal()\">\r\n              Cancel\r\n            </button>\r\n          </div>\r\n        </form>\r\n      </div>\r\n    </b-modal>\r\n\r\n-->\r\n\r\n    <b-modal ref=\"my-modal2\" id=\"modal-1\" size=\"sm\" :no-close-on-backdrop=\"true\" hide-footer title=\"BootstrapVue\">\r\n      <form @submit.stop.prevent=\"onSaveModal\">\r\n        <div>\r\n          <div class=\"form-group row\">\r\n            <label class=\"col-sm-4 col-form-label\">Id</label>\r\n            <div class=\"col-sm-8\">\r\n              <input type=\"text\" class=\"form-control\" v-model=\"r.Id\" />\r\n            </div>\r\n          </div>\r\n\r\n          <div class=\"form-group row\">\r\n            <label class=\"col-sm-4 col-form-label\">\r\n              Name\r\n            </label>\r\n            <div class=\"col-sm-8\">\r\n              <input \r\n                type=\"text\" \r\n                v-model=\"r.Name\"\r\n                v-validate=\"'required'\"\r\n                name=\"Name\"\r\n                :class=\" 'form-control' \" \r\n              />\r\n            </div>\r\n          </div>\r\n\r\n          <div class=\"form-group row\">\r\n            <label class=\"col-sm-4 col-form-label\">Tuổi</label>\r\n            <div class=\"col-sm-8\">\r\n              <input \r\n                type=\"text\" \r\n                :class=\" 'form-control' \" \r\n                v-model=\"r.Tuoi\"\r\n                v-validate=\"'required'\"\r\n                name=\"Tuoi\"\r\n              />\r\n            </div>\r\n          </div>\r\n\r\n          <div class=\"form-group row\">\r\n            <label class=\"col-sm-4 col-form-label\">Tp</label>\r\n            <div class=\"col-sm-8\">\r\n              <com-select-2 :options=\"options\" v-model=\"r.Tp\"  >\r\n                <option disabled value=\"0\">Select one</option>\r\n              </com-select-2>\r\n            </div>\r\n          </div>\r\n        </div>  \r\n        <div class=\"float-right\">\r\n          <button type=\"submit\" class=\"btn btn-primary mr-1 btn-sm\">Save</button>\r\n          <button type=\"button\" class=\"btn btn-sm\" @click=\"hideModal()\">Cancel</button>\r\n        </div>\r\n      </form>\r\n    </b-modal>\r\n\r\n    <button class=\"btn btn-primary btn-sm mt-1\" @click=\"onAdd\">Add</button>\r\n\r\n\r\n\r\n  </div>\r\n</template>\r\n<script>\r\n//import MyLoadSelect2 from \"@/func/MyLoadSelect2.js\";\r\n\r\nvar MyLoadSelect2 = {\r\n  test() {\r\n    return 'hello test';\r\n  },\r\n  loadCss(url) {\r\n    return new Promise((resolve, reject) => {\r\n      let link = document.createElement('link');\r\n      link.type = 'text/css';\r\n      link.rel = 'stylesheet';\r\n      link.onload = resolve;\r\n      link.href = url;\r\n      let headScript = document.querySelector('script');\r\n      headScript.parentNode.insertBefore(link, headScript);\r\n    });\r\n  },\r\n  loadScript(id, url) {\r\n    return new Promise(function (resolve, reject) {\r\n      var script = document.createElement(\"script\");\r\n      script.onload = resolve;\r\n      script.onerror = reject;\r\n      script.src = url;\r\n      script.type = \"text/javascript\";\r\n      script.setAttribute('id', id);\r\n      document.getElementsByTagName(\"head\")[0].appendChild(script);\r\n    });\r\n  },\r\n  loadMyScript(that) {\r\n    if (!document.getElementById(\"myScript\")) {\r\n      this.loadScript(\"myScript\", \"js/jquery.min.js\").then(() => {\r\n        this.loadScript(\"\", \"js/bootstrap.js\").then(() => {\r\n          this.loadScript(\"\", \"js/select2.min.js\").then(() => {\r\n            this.loadScript(\"\", \"js/bootstrap-datepicker.js\").then(() => {\r\n              //this.loadScript(\"\", \"js/moment.js\").then(()=>{\r\n              this.loadCss(\"css/select2.min.css\").then(() => {\r\n                this.loadCss(\"css/bootstrap-datepicker.css\").then(() => {\r\n                  that.onReady();\r\n                });\r\n              });\r\n              //});\r\n            });\r\n          });\r\n        });\r\n      });\r\n    } else {\r\n      that.onReady();\r\n    }\r\n  },\r\n}\r\n\r\nimport VeeValidate from \"vee-validate\";\r\nimport Vue from \"vue\";\r\nVue.use(VeeValidate, {\r\n  classes: true,\r\n});\r\n\r\nimport ComSelect2 from '@/components/ComSelect2.vue'\r\n\r\nexport default {\r\n  name: 'Tpl',\r\n  components: {\r\n    ComSelect2\r\n  },\r\n  data(){\r\n    return{\r\n      r:{\r\n        Id:1,\r\n        Name:\"thuc 101\",\r\n        Tuoi:100,\r\n        Tp:2\r\n      },\r\n      selected: 1,\r\n      options: [{ id: 1, text: \"Hello\" }, { id: 2, text: \"World\" }]\r\n    }\r\n  },\r\n  mounted() {\r\n    let that = this;\r\n  },\r\n  methods:{\r\n    onSaveModal(){\r\n      this.$validator.validateAll().then((result) => {\r\n        if (!result) {\r\n          this.toast(\"Không hợp lệ\");\r\n          return;\r\n        }else{\r\n          alert(JSON.stringify(this.r));\r\n          this.hideModal();\r\n        }\r\n      })\r\n    },    \r\n    onAdd(){\r\n      this.showModal();\r\n    },\r\n    toast(msg, variant = null) {\r\n      this.$bvToast.toast(msg, {\r\n        title: `Information`,\r\n        variant: variant,\r\n        solid: true,\r\n      });\r\n    },\r\n    showModal() {\r\n        this.$refs['my-modal2'].show()\r\n    },\r\n    hideModal() {\r\n      this.$refs['my-modal2'].hide()\r\n    },\r\n  }\r\n}\r\n</script>\r\n<style scoped>\r\n  .form-group {\r\n    margin-bottom: 0.3rem;\r\n  }  \r\n  /* form-control touched dirty invalid */\r\n  .invalid{\r\n    border: 1px solid red;\r\n  }\r\n\r\n</style>\r\n  ";