<script>
//import MyLoadSelect2 from "@/func/MyLoadSelect2.js";

var MyLoadSelect2 = {
  test() {
    return 'hello test';
  },
  loadCss(url) {
    return new Promise((resolve, reject) => {
      let link = document.createElement('link');
      link.type = 'text/css';
      link.rel = 'stylesheet';
      link.onload = resolve;
      link.href = url;
      let headScript = document.querySelector('script');
      headScript.parentNode.insertBefore(link, headScript);
    });
  },
  loadScript(id, url) {
    return new Promise(function (resolve, reject) {
      var script = document.createElement("script");
      script.onload = resolve;
      script.onerror = reject;
      script.src = url;
      script.type = "text/javascript";
      script.setAttribute('id', id);
      document.getElementsByTagName("head")[0].appendChild(script);
    });
  },
  loadMyScript(that) {
    if (!document.getElementById("myScript")) {
      this.loadScript("myScript", "js/jquery.min.js").then(() => {
        this.loadScript("", "js/bootstrap.js").then(() => {
          this.loadScript("", "js/select2.min.js").then(() => {
            this.loadScript("", "js/bootstrap-datepicker.js").then(() => {
              //this.loadScript("", "js/moment.js").then(()=>{
              this.loadCss("css/select2.min.css").then(() => {
                this.loadCss("css/bootstrap-datepicker.css").then(() => {
                  that.onReady();
                });
              });
              //});
            });
          });
        });
      });
    } else {
      that.onReady();
    }
  },
}

import VeeValidate from "vee-validate";
import Vue from "vue";
Vue.use(VeeValidate, {
  classes: true,
});

import ComSelect2 from '@/components/ComSelect2.vue'

export default {
  name: 'Tpl',
  components: {
    ComSelect2
  },
  data(){
    return{
      r:{
        Id:1,
        Name:"thuc 101",
        Tuoi:100,
        Tp:2
      },
      selected: 1,
      options: [{ id: 1, text: "Hello" }, { id: 2, text: "World" }]
    }
  },
  mounted() {
    let that = this;
  },
  methods:{
    onSaveModal(){
      this.$validator.validateAll().then((result) => {
        if (!result) {
          this.toast("Không hợp lệ");
          return;
        }else{
          alert(JSON.stringify(this.r));
          this.hideModal();
        }
      })
    },    
    onAdd(){
      this.showModal();
    },
    toast(msg, variant = null) {
      this.$bvToast.toast(msg, {
        title: `Information`,
        variant: variant,
        solid: true,
      });
    },
    showModal() {
        this.$refs['my-modal2'].show()
    },
    hideModal() {
      this.$refs['my-modal2'].hide()
    },
  }
}
</script>
