<template>
    <select>
      <slot></slot>
    </select>
</template>
<script>
  var MyLoadSelect2 = {
    loadCss(url) {
      return new Promise((resolve, reject) => {
        let link = document.createElement('link');
        link.type = 'text/css';
        link.rel = 'stylesheet';
        link.onload = resolve;
        link.href = url;
        let headScript = document.querySelector('script');
        headScript.parentNode.insertBefore(link, headScript);
      });
    },
    loadScript(id, url) {
      return new Promise(function (resolve, reject) {
        var script = document.createElement("script");
        script.onload = resolve;
        script.onerror = reject;
        script.src = url;
        script.type = "text/javascript";
        script.setAttribute('id', id);
        document.getElementsByTagName("head")[0].appendChild(script);
      });
    },
  }
  export default {
    props: ["options", "value"],
    mounted: function () {
      //let that = this;
      if (!document.getElementById("myComSelect111")) {
        MyLoadSelect2.loadCss("https://unpkg.com/select2@4.0.3/dist/css/select2.min.css").then(() => {
          MyLoadSelect2.loadScript("myComSelect111", "https://unpkg.com/jquery@3.1.1/dist/jquery.js").then(() => {
            MyLoadSelect2.loadScript("", "https://unpkg.com/select2@4.0.3/dist/js/select2.js").then(() => {
              var vm = this;
              $(this.$el)
                .select2({ data: this.options })
                .val(this.value)
                .trigger("change")
                .on("change", function () {
                  vm.$emit("input", this.value);
                });
            });
          })
        })
      }//end if
    },
    watch: {
      value: function (value) {
        $(this.$el)
          .val(value)
          .trigger("change");
      },
      options: function (options) {
        $(this.$el)
          .empty()
          .select2({ data: options });
      }
    },
    destroyed: function () {
      $(this.$el)
        .off()
        .select2("destroy");
    }
  }
</script>
